@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;500;600;700&display=swap);
html {
  box-sizing: border-box;
}
 *:before, *:after {
  box-sizing: inherit;
  }
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}

:root{
  --white:#fff;
  --black:#000;
}

body{
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  
  font-weight: 600;
  background-color: #fafafa;
}

/* Navbar css is in its own file */

.container{
  width:100%;
  padding:0 300px;
  margin-top:70px;
}

.Posts{
  width:66%;
  margin: 1rem 0;
  background-color: #fff;
  background-color: var(--white);
  border: 1px solid #efefef;
  padding: 0.6rem;
  border-radius: 4px;
}

.PostsDisplay{
  width: 66%;
  margin:1rem 0;
  background: #fff;
  background: var(--white);
}

.Sidebar{
  position: fixed;
  width: 30%;
  top:80px;
  padding-left: 1.5rem;
  right:2.5rem;
}

.Sidebar__user{
  display: flex;
  align-items: center;
}

.Sidebar__user--avatar{
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  color: var(--white);
  background-color: #f83a63;
  border-radius: 50%  ;
}

.Sidebar__user--name{
  padding-left:10px;
  text-transform: capitalize;
}

@media only screen and (max-width: 600px){
  .container{
    width:100%;
    padding:0 150px;
    margin-top:40px;
  }
  

.Sidebar{
  display: none;
}
}
