::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #8e8e8e;
  font-weight: 400;
  font-size: 1rem;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #8e8e8e;
  font-weight: 400;
  font-size: 1rem;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #8e8e8e;
  font-weight: 400;
  font-size: 1rem;
}
:-moz-placeholder { /* Firefox 18- */
  color: #8e8e8e;
  font-weight: 400;
  font-size: 1rem;
}

.Navbar__main{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height:3.5rem;
    background-color:var(--white);
    display: flex;
    padding-top:0;
    padding-left: 300px;
    padding-right: 205px;
    border-bottom: 1px solid #efefef;
    align-items: center;
  }

  .Navbar__first{
    width:30%;
  }

  .Navbar__first__logo{
    margin-top: 0.39rem;
  }

  .Navbar__first__logo img{
    object-fit: cover;
    display: block;
  }

  .Navbar__middle{
    width:30%;
    justify-content: center;
  }

  .Navbar__middle-search{
    position: relative;
  }

  .Navbar__search{
    background-color: #fafafa;
    border: 1px solid #d2dddd;
    border-radius: 4px;
    text-align: center;
    font-size: 1rem;
    padding:7px 25px
  }

  .Navbar__search--icon{
    position: absolute;
    left:81px;
    top:9px;
    font-size: 0.80rem;
    font-weight: 800;
    color: silver;
  }
  
  .Navbar__middle-search input{
    outline:none;
  }

  .Navbar__last{
    margin-left: 10%;
    width: 40%;
    display: flex;
    justify-content: flex-end;
  }

  .Navbar__last li{
    list-style-type: none;
    display: inline-block;
    margin: 0 10px;
  }

  .Navbar__last--icons{
    cursor: pointer;
    font-size: 1.5rem;
  }
  .Navbar__last--login{
    cursor: pointer;
    font-size: 1rem;
  }

  @media only screen and (max-width: 600px){
    .Navbar__main{
      position: fixed;
      top:0;
      left:0;
      width: 100%;
      height:3.5rem;
      background-color:var(--white);
      display: flex;
      padding-top:0;
      padding-left: 100px;
      padding-right: 75px;
      border-bottom: 1px solid #efefef;
      align-items: center;
    }
    
    
  }
.Model__main{
    width:100%;
    height:100vh;
    background: rgba(0,0,0,0.70);
    top:0;
    left:0;
    display: flex;
    position: fixed;
    z-index: 0;
    justify-content: center;
    align-items: center;
}

.Model__container{
    width: 400px;
    margin: auto;
    height:auto;
    border-radius: 4px;
    padding: 15px;
    background-color: white;
    position: relative;
}

.Model__form--group{
    margin:.5rem 0;
}

.Model__form--close{
    position: absolute;
    bottom:102%;
    left: 99%;
    font-size: 2.3rem;
    cursor: pointer;
    color: #f83a63;
    z-index: 1;
}

.Model__input{
    width: 100%;
    height: 2.5rem;
    padding: 0 .9rem;
    border: 1px solid #d2dbdb;
    outline: none;
    border-radius: 1px;
}

.btn{
    outline: none;
    border: none;
    color: #fff;
    height: 2.5rem;
    padding: 0 1rem;
    border-radius: 3px;
    cursor: pointer;
}

.btn-smart{
    width: 100%;
    background: rgb(66, 135, 245);
}

.Model__form--group img{
    position: relative;
    left: 3px;
}

.Model__form--group span{
    cursor:pointer;
    font-size: 1rem;
}

.Model__container--txt{
    position: absolute;
    bottom: 105%;
    color:var(--white);
    left:21%;
}
.Stories{
    display: flex;
    background: var(--white);
    width: 66%;
    padding: 10px 0;
    border:1px solid #efefef;
    border-radius: 5px;
    overflow:auto;
}
.Stories__info{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:0 0.5rem;
    cursor:pointer;
}

.Stories__img{
    width:80px;
    height: 85px;
    border-radius:50% ;
    overflow: hidden;
    border:2px solid #f83a63;
    padding: 1px; 
}   
.Stories__name{
    font-size: 0.7rem;
    margin-top: 4px;
}
.Stories__img span{
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
}

.Stories__img span img{
    object-fit: cover;
    width: 100%;
    height: 100%;
} 
.Posts__input--1{
    width: 100%;
    height: 2.5rem;
    padding:0 13px;
    outline:none;
    border:1px solid #efefef;
    border-radius:2px;
}
.Posts__upload{
    display: flex;
    align-items: center;
    margin-top:0.7rem;
}
.Posts__icon{
    font-size:2.2rem;
    cursor: pointer;
    color: #f83a63;
}
.Post__upload--file{
    display: none;
}
.Posts__input--2{
    flex:1 1;
    text-align: left;
}
.Posts__submit{
    flex: 1 1;
    text-align: right;
}

.btn-submit{
    background-color: #f83a63;
    color: #fff;
    border: none;
    text-decoration: none;
    padding: 5px 9px;
    border-radius: 3px;
    outline: none;
    font-size: 1.1rem;
    cursor: pointer;
}

.Post__upload--file--details{
    display: flex;
    text-align: end;
}
.PostsDisplay__Header{
    display: flex;
    background: #fff;
    align-items: center;
    padding:0.7rem;
}

.PostsDisplay__Header--avatar{
    border-radius: 50%;
    width:2.5rem;
    height:2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color:#fff;
    background: #f83a63;
    text-transform: uppercase;
}

.PostsDisplay__Header--username{
    margin-left:0.5rem;
    text-transform: capitalize;
}

.PostsDisplay__image{
    width: 100%;
    overflow: hidden;
}
.PostsDisplay__image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Comments{
    margin-top: 0.7rem;
}

.Comments__input{
    height: 2.5rem;
    width: 100%;
    outline: none;
    border-top:1px solid #efefef;
    border-bottom:1px solid #efefef;
    border-left:none;
    border-right:none;
    padding:0 1rem;
}

.Comments__main{
    display: flex;
    box-sizing: content-box;
    width: 100%;
    align-items: center;
    flex-wrap:wrap;
    padding:0 0 0.7rem 0.7rem;
}

.Comments__main--name{
    margin-right: 0.3rem;
    font-size: 14px;
    font-weight: 700;
    text-transform: capitalize;
    cursor: pointer;
    border-bottom: transparent solid 3px;
}

.Comments__main--name:hover{
    border-bottom: black solid 3px;
}

.Comments__main--comment{
    font-size: 14px;
    margin-left: 0.2rem;
    font-weight: 400;
}
 

